.Custgaler_sec{
    position: fixed !important;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh !important;
    overflow-y: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(0, 0, 0, 0.6);
}
.item{
    width: 100%;
}
img { pointer-events: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;}
.item_block{
    padding:  10px 10px;
    transition:  all 0.4s;
    border-radius: 20px;
}
.item_block img{
    border-radius: 20px;

}
.item_block:hover{
    position: relative;
    padding:  0;
    cursor: pointer;
}
.galerbody{
    margin-top: 120px;
    position: relative;
    padding-bottom: 150px;
    z-index: 999;
}
.galerbody_img{
    position: absolute;
    width: 300px;
    right: 0;
    top: 0;
}
.galerbody_container_MAIN{
    margin-left: auto;
    margin-right: auto;
    padding-top: 110px;
    padding-bottom: 110px;
}
.alice-carousel__stage-item{
    transition: 0.4s;
}
.renderPrevButton, .renderNextButton{
    display: flex;
    z-index: 10;
}
.renderPrevButton{
    margin-left: calc(100% - 50px);
    align-items: center;
    box-shadow: black;
}
.arrow_galer{
    width: 50px;
}
.renderNextButton{
    align-items: center;
}
.alice-carousel__prev-btn, .alice-carousel__next-btn{
    cursor: pointer;
}
.alice-carousel__prev-btn{

}
.alice-carousel__next-btn{

}
.cust_galer_container{
    width: 800px;
    height: 800px;
    z-index: 999;
    display: flex;
    flex-direction: column;
}
.cust_galer_container_top__close{
    float: right;
    fill: rgba(255, 255, 255, 0.59);
}
.cust_galer_container_top__close:hover{
    fill: rgb(255, 255, 255);
    transition: 0.4s;
}
.cust_galer_container_top__close:hover{
    cursor: pointer;
}
.cust_galer_container_top{
    height: 5%;
}
.cust_galer_container_main{
    height: 95%;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
}
.cust_galer_container_main__photo{
    width: 100% !important;
    background-size: cover !important;
}
.cust_galer_container_top__close{
    /* transform: rotate(45deg);  Equal to rotateZ(45deg) */
}
.cust_galer_container{
    position: relative;
    top: -20px;
}


.arrow_galer{
    fill: transparent;
}
.arrow_galer:hover{
    transition: 0.4s;
    cursor: pointer;
}
.cust_galer_container_main__info{
padding: 20px;
}
.cust_galer_container_bottom{
    position: absolute;
    bottom: -80px;
    width: 100%;
    display: flex;
    justify-content: center;
}
.cust_galer_container_bottom_block{
    display: flex;
    justify-content: space-around;

    cursor: pointer;
}
@media (max-width: 1500px) {
.galerbody_container_MAIN{
    width: 80vw;
}
}
.carousel__next-btn {
    padding: 0 !important;
}
.alice-carousel__prev-btn, .alice-carousel__next-btn {
    margin-top: 30px;
}

.arrow_galer_fst{
margin-left: auto;
    margin-right: 10px;
}
    .arrow_galer_sc{
        margin-right: auto;
    }
.galer_prev, .galer_next{
    padding: 15px;
}
.inside{
    width: 40px;
}
@media (max-width: 370px) {
    .cust_galer_container_bottom_block{
        display: flex;
        justify-content: center;
        width: 100%;
        cursor: pointer;
    }
    .renderPrevButton{
        margin-left: auto;
        position: relative;
        bottom: 0px;
        align-items: center;
    }
}


@media (max-width: 420px) {
    .cust_galer_container_top__close{
        height: 25px;
    }
}
@media (max-width: 980px) {
    .cust_galer_container{
        width: 640px;
        height: 640px;
    }
}
@media (max-width: 675px) {
    .cust_galer_container{
        width: 90vw;
        height: 90vw;
    }
    .cust_galer_container_top{
        height: auto;
        position: relative;
        top: -10px;
    }
    .cust_galer_container_bottom{
        position: absolute;
        bottom: -80px;
        width: 100%;
        display: flex;
        justify-content: center;
    }
}